import { useEffect, useState } from "react"
import CaseTable from "../components/case-table";
import NavBar from "../components/navbar";
import CaseForm from "../components/test-case-forms";
import { Employee, TestCase } from "../dtos"
import { getAllEmployees, getAllTestCases } from "../utils/fetches";


export default function TestCasesPage(){

    const [testCases,setTestCases] = useState<TestCase[]>([])

    useEffect(()=>{

        getAllTestCases().then(cases =>{
            if(cases){
                setTestCases(cases)
            }
        });

    },[]);


    return(<>
        <NavBar/>
        <h1>Test Case Dashboard</h1>
        <CaseForm testCases={testCases} onAddedTestCase={setTestCases}/>
        <CaseTable cases={testCases} />
    </>)
}