import { useEffect, useState } from "react";
import DefectList from "../components/defect-list";
import NavBar from "../components/navbar";
import { Defect } from "../dtos";
import { getAllDefects } from "../utils/fetches";


export default function DefectOverviewPage(){

    const [defects, setDefects] = useState<Defect[]>([]);

    function rerenderDefects(){
        setDefects([...defects])
    }

    useEffect(() => {

        getAllDefects().then(ds =>{
            if(ds){
                setDefects(ds)
            }
        })

    },[])


    return(<>
        <NavBar/>
        <h1>Defect Overview</h1>
        
        <DefectList defects={defects} onStatusChange={rerenderDefects}/>
    </>)
    

   
}