import { useRef, useState } from "react";
import { Matrix, MatrixForm, Requirement } from "../dtos";
import { sendMatrix } from "../utils/fetches";
import RequirementsCreationTable from "./requirement-table";


export default function MatrixCreationForm(){

    const [reqs, setReqs] = useState<Requirement[]>([]);
    const titleInput = useRef<any>(null);

    async function createMatrix(){

        const matrix:Matrix={
            matrixId:0,
            title:titleInput.current.value,
            requirements:reqs
        }
        const savedMatrix = await sendMatrix(matrix);
        if(savedMatrix){
            alert(`Matrix with ID ${savedMatrix.matrixId} created`)
        }
    }

    return(<>

        <h2>New matrix</h2>

        <label htmlFor="title">Matrix Title: </label>
        <input ref={titleInput} name="title" type="text"/>

        <fieldset>
            <legend>Requirements</legend>    
            <RequirementsCreationTable reqs={reqs} setReqs={setReqs}/>
        </fieldset>



        <button onClick={createMatrix}>Create Matrix</button>

    </>)
}