import Modal from "react-modal"
import { ChangeEvent, FormEvent, useState } from "react";
import { Defect, DefectForm, Level } from "../dtos";
import { sendDefect } from "../utils/fetches";
import NavBar from "../components/navbar";


export default function DefectReporter(){


    const [defectFormData,setDefectFormData] = useState<DefectForm>({dateReported: 0, desc: "", stepsToReproduce: "", severity: Level.High, priority: Level.High})
    const [isOpen,setOpen] = useState(false);
    const [defect,setDefect] = useState<Defect>()


    function handleDesc(event:ChangeEvent<HTMLTextAreaElement>){
        setDefectFormData({...defectFormData, desc:event.target.value})
    }

    function handleSteps(event:ChangeEvent<HTMLTextAreaElement>){
        setDefectFormData({...defectFormData, stepsToReproduce:event.target.value})
    }

    function handleSeverity(event: ChangeEvent<HTMLInputElement>){
        const value = Number(event.target.value);

        if(value === 1){
            setDefectFormData({...defectFormData, severity:Level.Low})
        }
        if(value === 2){
            setDefectFormData({...defectFormData, severity:Level.Medium})
        }
        if(value === 3){
            setDefectFormData({...defectFormData, severity:Level.High})
        }

    }

    function handlePriority(event: ChangeEvent<HTMLInputElement>){
        const value = Number(event.target.value);

        if(value === 1){
            setDefectFormData({...defectFormData, priority:Level.Low})
        }
        if(value === 2){
            setDefectFormData({...defectFormData, priority:Level.Medium})
        }
        if(value === 3){
            setDefectFormData({...defectFormData, priority:Level.High})
        }

    }

    function handleDate(event: ChangeEvent<HTMLInputElement>){
        const date = new Date(event.target.value);
        setDefectFormData({...defectFormData, dateReported:date.getTime()})
    }


    async function report(event: FormEvent<HTMLFormElement>){
        event.preventDefault()

        const answer = window.confirm("Confirm Bug Report?");
        if(answer){
            const defect = await sendDefect(defectFormData);
            if(defect){
                setDefect(defect);
                setOpen(true);
            }
        }

    }



    return(<>
    <NavBar/>
    <h1>Defect Reporter</h1>
    <form onSubmit={report} id="defectReport" >

        <label htmlFor="dateReported">Date Found: </label>
        <input name="dateReported" type="date" onChange={handleDate} required minLength={10}/>

        <br />

        <label htmlFor="desc">Description: </label>
        <textarea name="desc" onChange={handleDesc} required minLength={10}/>

        <br />

        <label htmlFor="stepsToReproduce">How to reproduce: </label>
        <textarea name="stepsToReproduce" onChange={handleSteps} required />

        <br />

        <label htmlFor="severity">Severity: </label>
        <input name="severity"  type="range" min={1} max={3} onChange={handleSeverity} required/>
        <LevelDisplay level={defectFormData.severity}/>
        <br />

        <label htmlFor="priority">Priority: </label>
        <input name="priority"  type="range" min={1} max={3} onChange={handlePriority} required/>
        <LevelDisplay level={defectFormData.priority}/>

        <br />

        <button type="submit" >Report</button>
    </form>

    <Modal isOpen={isOpen}>

        <h4>Defect {defect?.defectId} created</h4>
        
        <button onClick={()=>setOpen(false)}>Close</button>

    </Modal>
    </>)
}

export function LevelDisplay(props:{level:Level}){

    const {level} = props;

    switch(level){
        case Level.Low: return <p>LOW</p>
        case Level.Medium: return <p>Medium</p>
        case Level.High: return <p>High</p>
    }

}