import { useRef, useState } from "react";
import { Defect, Employee, Role } from "../dtos";
import { replaceDefect } from "../utils/fetches";


export default function DefectAssigner(props:{defects:Defect[], employees:Employee[], onAssignment:Function}){

    const {defects, employees, onAssignment} = props

    const [isOpen, setOpen]= useState(false)
    const [selectedDefect, setSelected] = useState<Defect>()
    const dataList = useRef<HTMLInputElement>(null)

    function selectDefect(defect: Defect){
        setSelected(defect)
        setOpen(true);
    }

    async function assign(){

        const defect = defects.find(d => d.defectId === selectedDefect?.defectId)

        if(defect){
            
            defect.assignedTo = dataList.current?.value;
            await replaceDefect(defect)
        }
        onAssignment();
        setOpen(false)
    }

    return(<>

        <table>
            <thead>
                <tr><th>Defect ID</th><th>Defect desc</th><th>Select</th></tr>
            </thead>
            <tbody>
                {defects.map(d => <tr key={d.defectId}>
                    <td>{d.defectId}</td>
                    <td>{d.desc}</td>
                    <td><button onClick={()=>selectDefect(d)}>Select</button></td>
                </tr>)}
                
            </tbody>
        </table>

        {isOpen &&
            <div>
                <h4>{selectedDefect?.desc}</h4>

                <input list="employees" ref={dataList} />

                <datalist id="employees" >
                    {employees.filter(e=>e.role === Role.TESTER)
                    .map(t => <option key={t.username} value={t.username}>{t.username}</option>)
                    }
                </datalist>

                <button onClick={assign}>Assign</button>

            </div>
        }

    
    </>)


}