import { ErrorMessage, Field, Form, Formik } from "formik";
import { TestCase, TestCaseForm, TestResult } from "../dtos";
import { addTestCase } from "../utils/fetches";


export default function CaseForm(props:{onAddedTestCase:React.Dispatch<React.SetStateAction<TestCase[]>>, testCases:TestCase[]}){

    

    const baseTestCase: TestCaseForm = {
        desc: "",
        steps: "",
        result: TestResult.UNEXECUTED,
        isAutomated: false,
        resultSummary: "",
        performedBy:"No One"
    }



    return(<>
        <Formik
       initialValues={baseTestCase}
       onSubmit={async function (values: TestCaseForm, {resetForm}){
    
            const testCase = await addTestCase(values);
    
            if(testCase){
                resetForm(baseTestCase as any)
                props.onAddedTestCase([...props.testCases, testCase])
            }else{
                alert("Failed to add Test case")
            }
        }}
     >
         <Form>

            <fieldset>
                <legend>Overivew</legend>

                <h4>Description</h4>
                <Field component="textarea" name="desc" />

                <h4>Steps</h4>
                <Field component="textarea" name="steps" />

                <button type="submit" > Submit</button>
            </fieldset>

         </Form>


     </Formik>
    </>)
}


