import axios from "axios";
import { DefectForm, Defect, LoginCredentials, Employee, TestCase, TestCaseForm, DefectStatus, Matrix, Requirement } from "../dtos";


const domain = "http://localhost:8000"

export async function sendDefect(defectForm:DefectForm): Promise<Defect | null>{

    const response = await axios.post<Defect>(`${domain}/defects`,defectForm);
    return response.status === 201 ? response.data : null;

}

export async function sendLoginCredentials(loginCredentials: LoginCredentials): Promise<Employee | null> {
    const response = await axios.patch<Employee>(`${domain}/login`, loginCredentials);
    return response.status === 200 ? response.data : null; 
}

export async function getAllDefects(): Promise<Defect[] | null>{
    const response = await axios.get<Defect[]>(`${domain}/defects`);
    return response.status === 200? response.data : null;
}

export async function getAllTestCases(): Promise<TestCase[] | null>{
    const response = await axios.get<TestCase[]>(`${domain}/cases`);
    return response.status === 200? response.data : null;
}

export async function getAllEmployees(): Promise<Employee[] | null>{
    const response = await axios.get<Employee[]>(`${domain}/employees`);
    return response.status === 200? response.data : null;
}

export async function getTestCaseById(id:number): Promise<TestCase | null>{
    const response = await axios.get<TestCase>(`${domain}/cases/${id}`);
    return response.status === 200? response.data : null;
}

export async function replaceTestCase(testCase:TestCase):Promise<TestCase |null>{
    const response = await axios.put<TestCase>(`${domain}/cases/${testCase.caseId}`, testCase);
    return response.status === 200? response.data : null;
}

export async function addTestCase(testCaseForm:TestCaseForm):Promise<TestCase | null>{
    const response = await axios.post<TestCase>(`${domain}/cases`, testCaseForm);
    return response.status ===201? response.data : null;
}

export async function changeDefectStatus(defectId: number, status:DefectStatus): Promise<boolean>{
    const response = await axios.patch<void>(`${domain}/defects/${defectId}`, {status});
    return response.status === 204;
}

export async function replaceDefect(defect:Defect):Promise<Defect | null> {
    const response = await axios.put<Defect>(`${domain}/defects`, defect);
    return response.status === 200? response.data : null;
    
}

export async function sendMatrix(matrix:Matrix):Promise<Matrix | null>{
    const response = await axios.post<Matrix>(`${domain}/matrices`, matrix);
    return response.status === 201? response.data : null;

}

export async function fetchAllMatrices(): Promise<Matrix[] | null> {
    const response = await axios.get<Matrix[]>(`${domain}/matrices`);
    return response.status === 200? response.data : null
}

export async function updateRequiruements(matrixId: number, requirements:Requirement[]){
    const response = await axios.put<Matrix>(`${domain}/matrices/${matrixId}/requirements`, requirements);
    return response.status === 200? response.data : null
}