import { useEffect, useState } from "react"
import Collapsible from "react-collapsible";
import MatrixUpdater from "../components/matrix-updater";
import NavBar from "../components/navbar";
import { Defect, Matrix, TestCase } from "../dtos"
import {fetchAllMatrices, getAllDefects, getAllTestCases} from '../utils/fetches'


export default function MatrixDashboardPage(){

    const [matrices,setMatrices] = useState<Matrix[]>([]);
    const [testCases,setTestCases] = useState<TestCase[]>([])
    const [defects, setDefects] = useState<Defect[]>([]);

 
    useEffect(()=>{
        fetchAllMatrices().then(ms => {
            if(ms){
                setMatrices(ms)
            }
        });
        getAllTestCases().then(cases =>{
            if(cases){
                setTestCases(cases)
            }
        });

        getAllDefects().then(ds =>{
            if(ds){
                    setDefects(ds)
            }
        })

    }, [])



    return(<>
        <NavBar/>
        <h1>Matrices</h1>

        <ul>
            {matrices.map(m =><li key={m.matrixId} >{m.title} <Collapsible trigger={<button>Show</button>}><MatrixUpdater defects={defects} testCases={testCases} matrix={m}/></Collapsible></li>)}
        </ul>
       

    </>)
}