import { ChangeEvent, useContext, useState } from "react"
import { useNavigate } from "react-router-dom"
import { EmployeeContext } from "../App"
import { LoginCredentials, Employee, Role } from "../dtos"
import { sendLoginCredentials } from "../utils/fetches"




export default function LoginPage(){

    const [credentials,setCredentials] = useState<Readonly<LoginCredentials>>({username:"", password:""})
    const employeeContext = useContext(EmployeeContext)
    const navigate = useNavigate();
    

    function updateUsername(event: ChangeEvent<HTMLInputElement>){
        setCredentials({...credentials, username:event.currentTarget.value})
    }

    function updatePassword(event: ChangeEvent<HTMLInputElement>){
        setCredentials({...credentials, password: event.currentTarget.value})
    }

    async function login(){

        const employee: Employee | null = await sendLoginCredentials(credentials);
        console.log(employee)
        if(employee){
            
            if(employeeContext){
                employeeContext.setEmployee(employee)

                if(employee.role === Role.MANAGER){
                    navigate("/managerhome");
                }else{
                    navigate("/testerhome");
                }
                
            }

        }else{
            alert("Wrong username or password")
        }

    }

    return(<>
    <h1>BugCatcher Login</h1>

    <fieldset>

        <label htmlFor="username">Username: </label>
        <input name="username" type="text" placeholder="gatorFan99" onChange={updateUsername} />
        <br />

        <label htmlFor="pass">Password: </label>
        <input name="pass" type="password" onChange={updatePassword} />
        <br />
        <button onClick={login} >Login</button>

    </fieldset>
    </>)
}