import { useState } from "react"
import Modal from "react-modal"
import { Link } from "react-router-dom";
import { TestCase, TestResult } from "../dtos"

export default function CaseTable(props:{cases:TestCase[]}){

    const {cases} = props;
    const [isModalOpen, setModalOpen] = useState(false);
    const [selectedCase, setCase] = useState<TestCase | null>(null)

    function openDetailsModal(testCase: TestCase){
        setCase(testCase);
        setModalOpen(true)
    }
    
    const Rows = ()=> <>{
        cases.map(c => <tr key={c.caseId}>
            <td>{c.caseId}</td>
            <td>{c.desc}</td>
            <td>{c.result}</td>
            <td><button onClick={()=>openDetailsModal(c)}>Details</button></td>
        </tr>)
        }</>

    return(<>
    <table>
        <thead>
            <tr>
                <th>ID</th>
                <th>Description</th>
                <th>Result</th>
                <th>Detailed View</th>
            </tr>
        </thead>
        <tbody>
           <Rows/>
        </tbody>

    </table>
    <Modal isOpen={isModalOpen}>
        <h3>Case {selectedCase?.caseId}</h3>

        <h4>Desc</h4>
        <p>{selectedCase?.desc}</p>

        <h4>Steps</h4>
        <p>{selectedCase?.steps}</p>

        <h4>Result</h4>
        <p>{selectedCase?.result}</p>

        <h4>Result Summary</h4>
        <p>{selectedCase?.resultSummary}</p>

        <h4>Automated</h4>
        <p>{selectedCase?.isAutomated ? "YES": "NO"}</p>

        <h4>Performed By</h4>
        <p>{selectedCase?.performedBy ?? "N/A"}</p>


        <button onClick={()=>setModalOpen(false)}>Close</button>
        <button><Link to={`/caseeditor/${selectedCase?.caseId}`}>Edit</Link></button>
    </Modal>

    </>)
}
