import { useState, useEffect } from "react";
import DefectAssigner from "../components/defect-assigner";
import NavBar from "../components/navbar";
import MatrixCreationForm from "../components/new-matrix-form";
import { TestCase, Defect, Employee } from "../dtos";
import { getAllTestCases, getAllDefects, getAllEmployees } from "../utils/fetches";


export default function ManagerHome(){

    const [defects, setDefects] = useState<Defect[]>([]);
    const [employees, setEmployees] = useState<Employee[]>([]);
    const [isFormVisible, setFormVisible] = useState(false);

    function rerenderDefects(){
        setDefects([...defects])
    }


    useEffect(()=>{


        getAllDefects().then(ds =>{
            if(ds){
                    setDefects(ds)
            }
        })

        getAllEmployees().then(es=>{
            if(es){
                setEmployees(es);
            }
        })

    },[]);

    return(<>
        <NavBar/>
        <h1>Manager Home</h1>

        <h3>Assign Defects</h3>
        <DefectAssigner defects={defects.filter(d=>!d.assignedTo)} employees={employees} onAssignment={rerenderDefects}/>
        {isFormVisible? <MatrixCreationForm/> : <button onClick={()=>setFormVisible(true)}>Create A new Requirements Matrix</button>}
        

    </>)
}