import { useRef, useState } from "react";
import { Defect, Matrix, Requirement, TestCase } from "../dtos";
import { updateRequiruements } from "../utils/fetches";


export default function MatrixUpdater(props:{matrix:Matrix, defects:Defect[], testCases:TestCase[]}){

    const {matrix, defects, testCases} = props;
    const [reqs, setReqs] = useState<Requirement[]>(matrix.requirements)
    const [story, setStory] = useState("")

    const defectInput = useRef<any>(null);
    const testcaseInput = useRef<any>(null);

   function addTestCase(id:number){

        reqs.find(r => r.userStoryOrRule === story)?.testCases.push(id)
        setReqs([...reqs])

   }

   function removeTestCase(id:number){
        const r = reqs.find(r => r.userStoryOrRule === story)
        if(r){
            r.testCases = r.testCases.filter(i => i !==id);
        }
        setReqs([...reqs])
   }

   function addDefect(id:number){

        reqs.find(r => r.userStoryOrRule === story)?.defectIds.push(id)
        setReqs([...reqs])

    }

    function removeDefect(id:number){
        const r = reqs.find(r => r.userStoryOrRule === story)
        if(r){
            r.defectIds = r.defectIds.filter(i => i !==id);
        }
        setReqs([...reqs])
    }

    async function saveRequirements(){
        const mat = await updateRequiruements(matrix.matrixId, reqs);
        if(mat){
            alert("Matrix Saved")
        }
    }
   


    return(<>

        <h4>Requirements</h4>
        <table>
            <thead>
                <tr>
                    <th>User Story Or Rule</th>
                    <th>Priority</th>
                    <th>Test Cases</th>
                    <th>Defects</th>
                    <th>Note</th>
                </tr>
            </thead>
            <tbody>
                {reqs.map(r => <tr>
                    <td>{r.userStoryOrRule}</td>
                    <td>{r.priority}</td>
                    <td>{r.testCases.join(", ")}</td>
                    <td>{r.defectIds.join(", ") || "None"}</td>
                    <td>{r.note}</td>
                    <td><button onClick={()=>setStory(r.userStoryOrRule)}>Edit</button></td>
                </tr>)}
            </tbody>
        </table>

        {reqs.find(r => r.userStoryOrRule === story) && <>
            <h3>Test Case IDs</h3>
        <ul>
            {reqs.find(r => r.userStoryOrRule === story)?.testCases.map(tc =><li>{tc} <button onClick={()=>removeTestCase(tc)}>Remove</button></li>)}
            {reqs.find(r => r.userStoryOrRule === story) && <li><input list="testlist" ref={testcaseInput} /><button onClick={()=>addTestCase(Number(testcaseInput.current.value))}>Add</button></li>}
        </ul>

        <h3>Defect IDs</h3>
        <ul>
            {reqs.find(r => r.userStoryOrRule === story)?.defectIds.map(d =><li>{d} <button onClick={()=> removeDefect(d)}>Remove</button></li>)}
            {reqs.find(r => r.userStoryOrRule === story) && <li><input list="defectlist" ref={defectInput} />< button onClick={()=>addDefect(Number(defectInput.current.value))}>Add</button></li>}
        </ul>
        
        </>
        }

        <button onClick={saveRequirements}>Save Requirements</button>

        <datalist id="testlist">
            {testCases.map(tc => <option key={tc.caseId} value={tc.caseId}>{tc.caseId}</option>)}
        </datalist>

        <datalist id="defectlist">
            {defects.map(tc => <option key={tc.defectId} value={tc.defectId}>{tc.defectId}</option>)}
        </datalist>

    </>)
}