import { createContext, useContext, useState } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";

import { Employee, Role } from "./dtos";
import CaseEditorPage from "./pages/case-editor";

import LoginPage from "./pages/login";
import ManagerHome from "./pages/manager-home";
import TestCasesPage from "./pages/test-cases";
import TesterHome from "./pages/tester-home";
import Modal from "react-modal"
import DefectReporter from "./pages/defect-reporter";
import DefectOverviewPage from "./pages/defect-overview";
import MatrixDashboardPage from "./pages/matrix.-dashboard";
Modal.setAppElement('#root');



export const EmployeeContext = createContext<{employee:Employee, setEmployee:React.Dispatch<React.SetStateAction<Employee>>} | null>(null)

export function useEmployeeContext(){

    return  useContext(EmployeeContext)
   
}

function App() {

  const [employee,setEmployee] = useState<Employee>({username:"",fname:"", lname:"", role: Role.TESTER})

  return (<><EmployeeContext.Provider value={{employee,setEmployee}}>
    <BrowserRouter>

    <Routes>
      <Route path="/" element={<LoginPage/>}/>
      <Route path="/managerhome" element={<ManagerHome/>}/>
      <Route path="/testerhome" element={<TesterHome/>}/>
      <Route path="/testcases" element={<TestCasesPage/>}/>
      <Route path='/caseeditor/:caseId' element={<CaseEditorPage/>}/>
      <Route path="/defectreporter" element={<DefectReporter/>}/>
      <Route path="/defectoverview" element={<DefectOverviewPage/>}/>
      <Route path="/matrices" element={<MatrixDashboardPage/>}/>

    </Routes>

   
  </BrowserRouter>
   </EmployeeContext.Provider></>
  );
}

export default App;
