import { useState, useEffect } from "react";
import { useEmployeeContext } from "../App";
import CaseTable from "../components/case-table";
import DefectList from "../components/defect-list";
import NavBar from "../components/navbar";
import { Defect, TestCase } from "../dtos";
import { getAllDefects, getAllTestCases } from "../utils/fetches";

export default function TesterHome(){

    const empContext = useEmployeeContext();
    const [testCases,setTestCases] = useState<TestCase[]>([])
    const [defects, setDefects] = useState<Defect[]>([]);

    function rerenderDefects(){
        setDefects([...defects])
    }


    useEffect(()=>{

        getAllTestCases().then(cases =>{
            if(cases){
                if(empContext && empContext.employee.username !== ""){
                    setTestCases(cases.filter(c =>c.performedBy === empContext.employee.username ?? ""))
                }
                
            }
        });

        getAllDefects().then(ds =>{
            if(ds){
                if(empContext && empContext.employee.username !== ""){
                    setDefects(ds.filter(d => d.assignedTo === empContext.employee.username))
                }

            }
        })

    },[]);

    return(<>
        <NavBar/>
        <h1>Tester Home</h1>

        <h3>My Defects</h3>
        <DefectList defects={defects} onStatusChange={rerenderDefects}/>


        <h3>My Test Cases</h3>
        <CaseTable cases={testCases}/>

    </>)
}