import { useRef, useState, KeyboardEvent } from "react";
import { Level, Requirement } from "../dtos";




export default function RequirementsCreationTable(props:{setReqs:React.Dispatch<React.SetStateAction<Requirement[]>>,reqs:Requirement[]}){


    const userStoryInput = useRef<any>(null);
    const priorityInput = useRef<any>(null);
    const noteInput = useRef<any>(null);
  

    const {reqs, setReqs} = props

    function addReq(){
        const req: Requirement = {
            userStoryOrRule: userStoryInput.current.value,
            priority: priorityInput.current.value,
            note: noteInput.current.value,
            testCases:[],
            defectIds:[]
        }
        setReqs([...reqs, req])
    }

    return(<><table>
        <thead>
            <th>User Story/Rule</th>
            <th>Priority</th>
            <th>Note</th>
        </thead>
        <tbody>

                {reqs.map( r => <tr >
                    <td>{r.userStoryOrRule}</td>
                    <td>{r.priority}</td>
                    <td>{r.note}</td>
                    </tr>)}

            <tr>
                <td><input ref={userStoryInput} type="text" placeholder="User Story" /></td>

                <td>
                    <select ref={priorityInput}>
                        <option value={Level.Low}>Low</option>
                        <option value={Level.Medium}>Medium</option>
                        <option value={Level.High}>High</option>
                    </select></td>
                <td><input ref={noteInput} type="text" placeholder="Note"/></td>
            </tr>
        </tbody>
    </table>
    <button onClick={addReq}>Add Requirement</button>


    </>)
}