import { ChangeEvent, useEffect, useState } from "react";
import { useParams } from "react-router-dom"
import NavBar from "../components/navbar";
import TesterNavBar from "../components/navbar";
import { Employee, Role, TestCase, TestResult } from "../dtos"
import { getAllEmployees, getTestCaseById, replaceTestCase } from "../utils/fetches";


export default function CaseEditorPage(){

    const {caseId} = useParams();
    
    

    const [testCase, setTestCase] = useState<TestCase | null>(null);
    const [isDisabled,setDisabled] = useState(true)
    const [employees, setEmployees] = useState<Employee[] | null>(null);
    const [serverCase, setServerCase] = useState<TestCase | null>(null);

    useEffect(()=>{
        getTestCaseById(Number(caseId)).then(c => {
            if(c){
                setTestCase(c)
                setServerCase({...c})
                getAllEmployees().then(emps=>{
                    if(emps){
                        setEmployees(emps)
                    }
                })
            }
        });
    },[]);

    function edit(){
        setDisabled(false)
    }

    function reset(){
        if(testCase && serverCase){
            setTestCase({...serverCase})
        }  
        setDisabled(true)
    }

    async function save(){
        if(testCase){

            const answer = window.confirm("Are you sure you want to update the test case?");
            
            if(answer){
                const returnedTestCase = await replaceTestCase(testCase)
                if(returnedTestCase){
                    setServerCase(returnedTestCase);
                    setTestCase(returnedTestCase);
                    setDisabled(true)
                    alert("Test Case has been Saved")
                }
            }
           
        }
    }



    function descHandler(event: ChangeEvent<HTMLTextAreaElement>){
        if(testCase){
            setTestCase({...testCase, desc:event.target.value})
        }
    }

    function stepsHandler(event: ChangeEvent<HTMLTextAreaElement>){
        if(testCase){
            setTestCase({...testCase, steps: event.target.value})
        }
    }

    function summaryHandler(event: ChangeEvent<HTMLTextAreaElement>){
        if(testCase){
            setTestCase({...testCase, resultSummary: event.target.value})
        }
    }

    function automatedHandler(event: ChangeEvent<HTMLInputElement>){
        if(testCase){
            setTestCase({...testCase, isAutomated:!testCase.isAutomated})
        }

    }

    function resultHandler(event:ChangeEvent<HTMLSelectElement>){
        if(testCase){
            setTestCase({...testCase, result:event.target.value as TestResult})
        }
    }

    function performedByHandler(event: ChangeEvent<HTMLSelectElement>){
        if(testCase){
            setTestCase({...testCase, performedBy:event.target.value})
        }
    }

    if(!testCase){
        
        return(<>
            <NavBar/>
            <h1>Test Case ID: {caseId} was not found</h1>
        </>)

    }else{

        return(<>
            <NavBar/>
            <h1>case id {caseId}</h1>

        
                <fieldset>
                    <legend>Overview</legend>

                    <h6>Description</h6>
                    <textarea disabled={isDisabled} value={testCase.desc} onChange={descHandler}/>

                    <h6>Steps</h6>
                    <textarea disabled={isDisabled} value={testCase.steps} onChange={stepsHandler}/>

                    <h6>Automated</h6>
                    {isDisabled?
                        <p>{testCase.isAutomated? "YES": "NO"}</p>:
                        <input type="checkbox" defaultChecked={testCase.isAutomated} onChange={automatedHandler} />}
                    

                    <h6>Performed by</h6>
                    {isDisabled?<p>{testCase.performedBy ?? "N/A"}</p>:
                        <select onChange={performedByHandler}>
                            <option value={"N/A"}> Not Applicable</option>
                            {employees?.filter(e => e.role === Role.TESTER).map(e => <option key={e.username} value={e.username}>{e.username}</option>)}
                        </select>
                    }
                    

                </fieldset>

                <fieldset>
                    <legend>Results</legend>

                    <h6>Test Result</h6>
                    {isDisabled?<p>{testCase.result}</p>:
                        <select onChange={resultHandler}>
                            <option value={TestResult.PASS}>PASS</option>
                            <option value={TestResult.FAIL}>FAIL</option>
                            <option value={TestResult.FLAKY}>FLAKY</option>
                            <option value={TestResult.UNEXECUTED}>UNEXECUTED</option>
                        </select>}
                    

                    <h6>Summary</h6>
                    <textarea disabled={isDisabled} value={testCase.resultSummary ?? "No summary provided"} onChange={summaryHandler}/>

                </fieldset>
                
                
                {isDisabled?<button onClick={edit}>Edit</button>:<button onClick={reset}>Reset</button> }   
                {!isDisabled && <button onClick={save}>Save</button>   }
                 
            
        </>)
    }



}