import Collapsible from "react-collapsible"
import { Defect, DefectStatus, Level } from "../dtos"
import { LevelDisplay } from "../pages/defect-reporter"
import { changeDefectStatus } from "../utils/fetches";


export default function DefectList(props:{defects:Defect[], onStatusChange:Function}){

    const {defects, onStatusChange} = props

    async function changeStatus(defect:Defect, status:DefectStatus){
        defect.status = status;
        const isSuccess = await changeDefectStatus(defect.defectId,status);
        if(isSuccess){
            onStatusChange();
        }else{
            alert("There was an error in making the change")
        }
        
    }

    return(<>
    <ul>
        {defects.map(d=><li key={d.defectId}>
            <Collapsible trigger={<p><b>ID: {d.defectId} </b>{d.desc} <b>{d.status}</b> +</p>}>
                
                <Collapsible trigger={<button>Change Status</button>}>
                    <button onClick={()=>changeStatus(d, DefectStatus.Accepted)}>Accepted</button>
                    <button onClick={()=>changeStatus(d, DefectStatus.Rejected)}>Rejected</button>
                    <button onClick={()=>changeStatus(d, DefectStatus.Fixed)}>Fixed</button>
                    <button onClick={()=>changeStatus(d, DefectStatus.Declined)}>Declined</button>
                    <button onClick={()=>changeStatus(d, DefectStatus.Shelved)}>Shelved</button>

                </Collapsible>

                <p>Severity:</p>
                <LevelDisplay level={d.severity}/>
                <p>Priority: </p>
                <LevelDisplay level={d.priority}/>

                <Collapsible trigger={<button>Steps</button>}>
                    <textarea  disabled value={d.stepsToReproduce}/>
                </Collapsible>

                <p>Assigned To: {d.assignedTo || "Unassigned"}</p>
                
                <p>Date Reported: {new Date(d.dateReported).toLocaleDateString()}</p>
       
            </Collapsible>

        </li>)}
    
    
    </ul>

    </>)
}