import { Link } from "react-router-dom";
import { useEmployeeContext } from "../App";
import { Role } from "../dtos";


export default function NavBar(){
    
    const empContext = useEmployeeContext()

    if(empContext){
        return(<nav>
            <Link to="/matrices">Matrices</Link>
            <Link to="/testcases">Test Cases</Link>
            <Link to="/defectreporter">Report a Defect</Link>
            <Link to="/defectoverview">Defect Overview</Link>
            <p>Welcome {empContext.employee.fname} {empContext.employee.lname} {empContext.employee.fname === "" && "Not signed in"} </p>
            {empContext.employee.role === Role.MANAGER? <Link to="/managerhome">Home</Link>:<Link to="/testerhome">Home</Link> }
        </nav>)
    }else{

        return(<nav>
            <Link to="/matrices">Matrices</Link>
            <Link to="/testcases">Test Cases</Link>
            <Link to="/defectreporter">Report a Defect</Link>
            <Link to="/defectoverview">Defect Overview</Link>
        </nav>)

    }

   
    


}